<template>
  <div style="background-color: #f8f9fa">
    <el-container style="height: 100vh">
      <el-aside width="auto">
        <project-aside></project-aside>
      </el-aside>
      <el-container>
        <el-header height="auto">
          <project-header></project-header>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>

  </div>
</template>

<script>
import ProjectAside from "@/components/ProjectAside";
import ProjectHeader from "@/components/ProjectHeader";
import {getProject, getStrings} from '../../api'


export default {
  data() {
    return {}
  },
  components: {
    ProjectAside,
    ProjectHeader
  },
  mounted() {
    console.log()
    if (this.$route.params.id !=
        this.$store.state.tab.projectData.projectId) {
      const request = {
        "projectId": this.$route.params.id,
      };

      getProject(request).then((data) => {
        if (data.code == 200) {
          this.$store.commit('updateProjectData', data.data)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .el-header {
    padding: 0;
  }
</style>